import fetch from './fetch';
import {host} from '@/assets/js/global';

export default {
  // 获取讲师列表
  list(params) {
    return fetch.get('/teachers', params);
  },
  // 获取老师详细信息
  getTeacherInfo(params) {
    return fetch.get('/web/teacherInfo', params, host.ichazuo);
  },
}



