import fetch from './fetch';
import {host} from '@/assets/js/global';


export default {
  // 获取内训课程详情
  getCourseOutline(params) {
    return fetch.get('/web/trainingInfo', params, host.ichazuo)
  },
  // 获取精品课列表
  getCourseList(params) {
    return fetch.get('/Site/getOfflineCourseList', params, host.hdk);
  },
  // 获取线下课程详细信息
  getCourseInfo(params) {
    return fetch.get('/web/offlineInfo', params, host.ichazuo);
  },
  // 培训需求留言
  saveCompanyInfo(params){
    return fetch.get('/Web/saveCompanyInfo', params, host.hdk);
  }
}



