<template>
  <div class="login-wrapper df dfdir dfc">
    <div class="el-container">
      <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" class="demo-ruleForm">
        <h1 class="form-title" v-if="loginType==2||loginType==3">登陆</h1>
        <h1 class="form-title" v-if="loginType==1">注册</h1>
        <p class="form-desc" v-if="loginType==2||loginType==3" @click="loginType=1">没有账号？可以去<span
          class="register">注册</span></p>
        <p class="form-desc" v-if="loginType==1" @click="loginType=3">已有账号？去<span class="register">登陆</span></p>

        <el-form-item prop="mobile">
          <el-input type="text" v-model="ruleForm.mobile" autocomplete="off" placeholder="请输入手机号"></el-input>
        </el-form-item>

        <!-- 注册 -->
        <el-col :span="24" v-if="loginType==1">

          <el-form-item :inline="true">
            <el-row :gutter="20">
              <el-col :span="15">
                <el-form-item prop="code">
                  <el-input type="text" v-model="ruleForm.code" autocomplete="off" placeholder="请输入验证码"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="9">
                <el-form-item class="df">
                  <!--<el-button class="demo-form-inline qrcode-btn" size="medium" :type="countdown>0?'info':'primary'" @click="getNoteCode('ruleForm')">-->
                  <!--<span v-if="countdown>0">{{countdown}}s</span>-->
                  <!--<span v-else>获取验证码</span>-->
                  <!--</el-button>-->
                  <div class="qrcode-btn" :class="countdown>0?'info':'primary'" @click="getNoteCode('ruleForm')">
                    <span v-if="countdown>0">重新获取 {{countdown}}</span>
                    <span v-else>获取验证码</span>
                  </div>
                </el-form-item>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item prop="password">
            <el-input type="password" v-model="ruleForm.password" autocomplete="off"
                      placeholder="密码"></el-input>
          </el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')">注册</el-button>
        </el-col>

        <!-- 密码登录 -->
        <el-col v-if="loginType==2">
          <el-form-item v-if="loginPass" prop="password">
            <el-input type="password" v-model="ruleForm.password" autocomplete="off"
                      placeholder="密码"></el-input>
          </el-form-item>
          <el-button size="medium" type="primary" @click="submitForm('ruleForm')">登录</el-button>
        </el-col>

        <!-- 验证码登录 -->
        <el-col v-if="loginType==3">
          <el-row :gutter="20">
            <el-col :span="15">
              <el-form-item prop="code">
                <el-input type="text" v-model="ruleForm.code" autocomplete="off" placeholder="请输入验证码"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="9">
              <el-form-item class="df">
                <!--<el-button class="demo-form-inline" size="medium" :type="countdown>0?'info':'primary'"-->
                           <!--@click="getNoteCode('ruleForm')">-->
                  <!--<span v-if="countdown>0">{{countdown}}s</span>-->
                  <!--<span v-else>获取验证码</span>-->
                <!--</el-button>-->
                <div class="qrcode-btn" :class="countdown>0?'info':'primary'" @click="getNoteCode('ruleForm')">
                  <span v-if="countdown>0">重新获取 {{countdown}}</span>
                  <span v-else>获取验证码</span>
                </div>
              </el-form-item>
            </el-col>
          </el-row>
          <el-button size="medium" type="primary" @click="submitForm('ruleForm')">登录</el-button>
        </el-col>

        <!-- 账号密码登录 -->
        <el-col class="footer-desc" v-if="loginType!=1">
          <span class="register" v-if="loginType==3" @click="loginType=2">账号密码登录</span>
          <span class="register" v-if="loginType==2" @click="loginType=3">短信登录</span>
        </el-col>
      </el-form>
      <span class="close iconfont" @click="close"></span>
    </div>
  </div>
  </div>
</template>

<script>
  import {mapState, mapActions} from 'vuex';
  import fetch from '@/api/fetch';
  import {host, regMobile} from '@/assets/js/global';


  export default {
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入密码'));
        } else {
          callback();
        }
      };
      var validateMobile = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请输入手机号'));
        } else if (!regMobile.test(value)) {
          callback(new Error('手机格式不正确'));
        } else {
          callback();
        }
      };
      var validateCode = (rule, value, callback) => {
        if (!value) {
          callback(new Error('请输入验证码'));
        } else if (value.length < 4) {
          callback(new Error('格式错误'));
        } else {
          callback();
        }
      };
      return {
        ruleForm: {
          mobile: '18610500240',
          password: ''
        },
        rules: {
          password: [
            {validator: validatePass, trigger: 'blur'}
          ],
          mobile: [
            {validator: validateMobile, trigger: 'blur'}
          ],
          code: [
            {validator: validateCode, trigger: 'blur'}
          ]
        },
        // 注册
        loginType: 3, // 1注册、2密码登录、3验证码登录
        // 密码登录
        loginPass: true,
        // 验证码倒计时
        countdown: 0,
      };
    },
    methods: {
      ...mapActions({
        login: 'user/login',
        register: 'user/register',
      }),
      submitForm(formName) {
        console.log(formName);
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const loginType = this.loginType
            switch (loginType) {
              case 1: // 注册
                console.log(loginType)
                this.toRegister(this.ruleForm)
                break;
              case 2: // 密码登录
                console.log(loginType)
                this.toLogin(this.ruleForm, 1)
                break;
              case 3: // 验证码登录
                console.log(loginType)
                this.toLogin(this.ruleForm, 2)
                break;
            }
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      /**
       * 登录
       * @param loginType 登录方式: 1账号密码登录、2短信登录
       */
      toLogin(ruleForm, loginType) {
        ruleForm['loginType'] = loginType
        this.login(ruleForm).then(res => {
          this.$ls.set('CZXY_TOKEN', res.data.token);
          // 跳转
          this.$message.success("登录成功！")
          setTimeout(()=> {
            window.location.href = '/'
          }, 1000)
        }).catch(err => {
          console.log('err===>', err)
        })
      },
      // 注册
      toRegister(ruleForm) {
        this.register(ruleForm).then(res => {
          this.$ls.set('CZXY_TOKEN', res.data.token);
          // 跳转
          this.$message.success("注册成功！")
        }).catch(err => {
          console.log('err===>', err)
          this.loginType = 3
        })
      },
      /**
       * 短信登录
       */
      // 获取短信
      getNoteCode(formName) {
        // 验证手机号
        this.$refs[formName].validateField('mobile', errorMessages => {
          if (!errorMessages && this.countdown == 0) {
            fetch.get('/Membernew/sendBusinessCodeByWeb', {
              type: 2, // 1检测是好多课用户后发送验证码，2不检测直接发
              mobile: this.ruleForm.mobile
            }, host.hdk).then(res => {
              this.$message.success(res.data.msg)
              this._countdown()
            })
          }
        })
      },
      /**
       * 验证码倒计时
       * @param time 扫计时秒数
       */
      _countdown(countdown = 60) {
        this.countdown = countdown
        clearInterval(inter)
        const inter = setInterval(()=> {
          if (this.countdown > 1) {
            this.countdown -= 1
          } else {
            this.countdown = 0
            clearInterval(inter)
          }
        }, 1000)
      },
      close() {
        console.log('close')
        this.$emit('close')
      }
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>

  .login-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
    background-color: rgba(0, 0, 0, .3);
    .el-container {
      position: relative;
      padding: 72px 36px;
      width: 490px;
      max-width: 90%;
      font-size: 14px;
      background-color: #fff;
      border-radius: 2px;
      box-sizing: border-box;
      .el-button {
        float: right;
      }
      .form-title {
        margin-bottom: 10px;
        font-size: 36px;
        font-weight: bold;
      }
      .form-desc {
        margin-bottom: 40px;
        color: #666;
        font-size: 24px;
        cursor: pointer;
        .register {
          color: #06609A;
        }
      }
      .el-form-item__content {
        margin-left: 0;
      }
      .qrcode-btn {
        padding: 0 16px;
        font-size: 20px;
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        border-radius: 2px;
        &.primary {
          border: 2px solid rgba(6, 96, 154, 1);
          color: #06609A;
        }
        &.info {
          border: 2px solid rgba(195, 195, 195, 1);
          color: #CECECE;
        }
      }
      .close {
        position: absolute;
        right: 12px;
        top: 12px;
        font-size: 20px;
        padding: 20px;
        color: #777777;
        cursor: pointer;
      }
    }
  }
</style>

<style>
  .el-container .el-input__inner {
    border-radius: 0;
    line-height: 50px;
  }
  .el-container .el-form-item__content {
    line-height: 50px;
  }
  .login-wrapper .el-container .el-button {
    width: 100%;
    height: 64px;
    font-size: 24px;
    background:rgba(6,96,154,1);
    border-radius: 2px;
    border: none;
    font-weight: 500;
  }
  .footer-desc {
    margin-top: 30px;
    color: #06609A;
  }
  @media screen and (min-width: 200px) and (max-width: 768px) {
    .login-wrapper .el-container {
      width: 85%!important;
    }
  }
</style>



