/**
 * 获取地址参数
 *
 * @param {string} data N 链接
 * @returns JSON
 */
export const getUrlParameter = data => {
  let query = {}
  let url = data
    ? data.substr(data.indexOf('?'))
    : document.location.search.replace('?', '')
  let urlParams = []
  if (url) {
    urlParams = url.split('&')
    urlParams.forEach(item => {
      let value = item.split('=')
      query[value[0]] = value[1]
    })
  }
  return query
}

// ApiHost
const getApiHost = (() => {
  const query = getUrlParameter()
  if (query.goHost) {
    return query.goHost
  } else {
    return 'sslapi3'
  }
})()

// 接口正服域名
export const host = {
  ichazuo: `//admin.ichazuo.cn/api/`,
  hdk: `https://${getApiHost}.ichazuo.cn`,
  java: '//www.chazuomba.com/iserver/app/',
  order: 'https://order.ichazuo.cn',
  node: 'https://node.ichazuo.cn',
  py: 'https://liveapi2.ichazuo.cn'
}


// 手机验证
export const regMobile = /^[0|2-9]\d{4,16}|1\d{10}$/;




