import course from '../../api/course'

const state = {
  // 内训课程详情
  outline: {},
  // 精品课列表
  excellentCourseList: {},
  // 线下课详细信息
  courseDetails: {},
};

const mutations = {
  CHANGE_COURSE(state, list) {
    let newList = JSON.parse(JSON.stringify(state.excellentCourseList))
    newList.data.list = list
    state.excellentCourseList = newList
  }
};

const actions = {
  // 内训课程详情
  async getTeachersList({state, commit}, params) {
    let res = await course.getCourseOutline(params)
    state.outline = res.data
    return res.data
  },
  // 获取精品课列表
  async getCourseList({state, commit}, params) {
    let res = await course.getCourseList(params)
    state.excellentCourseList = res.data
    return res
  },
  // 线下课详细信息
  async getCourseInfo({state, commit}, params) {
    let res = await course.getCourseInfo(params)
    state.courseDetails = res.data
    return res.data;
  },

  // 培训需求留言
  async saveCompanyInfo({state, commit}, params) {
    let res = await course.saveCompanyInfo(params)
    return res
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
}



