import user from '../../api/user'
import * as types from '../mutation-types'

const state = {
  userAuth: null
}

const mutations = {
  SET_USER_INFO(state, data) {
    state.userAuth = data
  }
}

const actions = {
  // 用户登录
  login({state, commit}, params) {
    return user.login(params);
  },

  // 用户注册
  register({state, commit}, params) {
    return user.register(params);
  },

  // 获取当前用户信息
  async auth({state, commit}, params) {
    const res = await user.auth(params);
    commit('SET_USER_INFO', res.data.data);
    return res;
  }
}

export default {
  namespaced: true,
  state,
  actions,
  mutations
}



