<template>
  <section>
    <section class="header">
      <header class="header-box">
        <section class="logo">
          <a href="/"><img src="../assets/images/LOGO.png" alt="LOGO"></a>
        </section>

        <div class="df dfaic">
          <ul class="nav-dropdown">
            <li class="nav-item">
              <el-dropdown trigger="click">
                  <span class="el-dropdown-link">
                    首页<i class="el-icon-arrow-down el-icon--right"></i>
                  </span>
                <el-dropdown-menu slot="dropdown">
                  <el-dropdown-item
                    v-for="(item, index) in nav"
                    :key="index"
                    @click.native="changeNav(item.path, index)"
                  >
                    <i :class="`icon ${item.icon}`"></i> {{item.name}}
                  </el-dropdown-item>
                </el-dropdown-menu>
              </el-dropdown>
            </li>
          </ul>

          <div class="nav-list">
            <ul class="nav-box">
              <li v-for="(item, index) in nav"
                  :class="navIndex === index ? 'nav-item nav-item--active' : 'nav-item'"
                  @click="changeNav(item.path, index)"
                  :key="index">
                <i :class="`icon ${item.icon}`"></i> {{item.name}}
              </li>
            </ul>
          </div>

          <div v-if="!userInfo.name" class="login-btn" @click="showLogin=true">
            <el-button type="primary" size="small">登录</el-button>
          </div>
          <div class="user-wrapper" v-if="userInfo.name">
            <img :src="userInfo.avatar" width="100%" height="100%">
          </div>
        </div>
      </header>
    </section>
    <!-- 登录 -->
    <Login v-if="showLogin" @close="showLogin=false"></Login>
  </section>
</template>

<script>
  import {mapState, mapActions} from 'vuex'
  import merge from 'webpack-merge'
  import Login from './Login'

  export default {
    props: ['userInfo'],
    data() {
      return {
        keyword: '',
        navIndex: 0,
        nav: [
          {name: '首页', path: '/', icon: 'el-icon-house'},
          {name: '内训讲师', path: '/', icon: 'el-icon-reading\n'},
          {name: '公开课', path: '/about', icon: 'el-icon-chat-round'},
        ],
        showLogin: false
      }
    },
    computed: {
    },
    mounted() {
    },
    methods: {
      ...mapActions({
        showUserManager: 'user/showUserManager',
        getArticleList: 'article/getArticleList'
      }),

      /**
       * 切换导航栏
       */
      changeNav(path, index) {
        this.$router.replace({
          query: merge({})
        });
        this.navIndex = index;
        this.toPath(path);
      },
      /**
       * 路由调整
       * @param path 路由地址
       */
      toPath(path) {
        this.$router.push(path);
      }
    },
    components: {
      Login
    }
  }
</script>

<style lang="scss" rel="stylesheet/scss" scoped>
  .header {
    width: 100%;
    height: 96px;
    z-index: 5000;
    background: #fff;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, .03);
    border-bottom: 1px solid #f0f0f0;
  }

  .header-box {
    width: 85%;
    box-sizing: border-box;
    margin: 0 auto;
    height: 96px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .logo {
    width: 160px;
    height: 96px;
    display: flex;
    align-items: center;

    & > a {
      display: inline-block;
      & img {
        width: 100%;

      }
    }
  }

  .nav-box-dropdown {
    display: none;
  }

  .nav-list {
    display: block;
    margin: 0 24px;

    & .nav-box {
      display: flex;
      height: 96px;
      width: 100%;

      & .nav-item {
        cursor: pointer;
        display: flex;
        align-items: center;
        justify-items: center;
        padding: 0 24px;
        height: 96px;
        line-height: 96px;
        color: #404040;
        font-size: 20px;
        text-decoration: none;

        &:hover {
          color: #409EFF;
        }
      }

      & .icon {
        margin-right: 8px;
      }

      & .nav-item--active {
        color: #409EFF;
      }
    }
  }

  .nav-dropdown {
    display: none;
  }

  .user-wrapper {
    width: 60px;
    height: 60px;
    border-radius: 100%;
    overflow: hidden;
    cursor: pointer;
  }

  @media screen and (min-width: 200px) and (max-width: 768px) {

    .header {
      box-sizing: border-box;
      padding: 0 16px;
    }
    .header-box {
      width: 100%;
    }
    .nav-dropdown {
      display: block;
      margin: 0 16px;
    }

    .nav-list {
      display: none;
      margin: 0 16px;
    }

  }
</style>



