import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  // 哈斯
  mode: 'history',
  scrollBehavior(to, from, savedPosition) {
    // 兼容
    if (savedPosition) {
      return savedPosition
    } else {
      return {x: 0, y: 0}
    }
  },
  routes: [
    {
      // 首页
      path: '/',
      meta: {
        title: "首页",
        noAuth: true
      },
      component(resolve) {
        require(['../views/training/index.vue'], resolve);
      }
    },
    {
      path: '/login',
      meta: {
        title: "登录",
        noAuth: true
      },
      component: (resolve) => require(['../views/login.vue'], resolve),
    },
    {
      // 内训讲师列表
      path: '/training/lecturer',
      component(resolve) {
        require(['../views/training/lecturer/list.vue'], resolve);
      }
    },
    {
      // 讲师详情
      path: '/training/lecturer/:id',
      component(resolve) {
        require(['../views/training/lecturer/index.vue'], resolve);
      }
    },
    // {
    //     // 课程列表
    //     path: '/training/courses',
    //     component(resolve) {
    //       require(['../views/training/courses/list.vue'], resolve);
    //     }
    // },
    {
      // 线下课详情
      path: '/training/courses/:id',
      component(resolve) {
        require(['../views/training/courses/index.vue'], resolve);
      }
    },

    {
      // 内训课详情
      path: '/training/courses/outline/:id',
      name: '课程大纲',
      component(resolve) {
        require(['../views/training/courses/outline.vue'], resolve);
      }
    },

    {
      // 公开课
      path: '/about',
      component(resolve) {
        require(['../views/about/index.vue'], resolve);
      }
    }
  ]
})



