import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store';
import VueLocalStorage from 'vue-ls';
import 'lib-flexible/flexible';
import VueLazyLoad from 'vue-lazyload'
import mavonEditor from 'mavon-editor'
import 'mavon-editor/dist/css/index.css'
import './assets/css/flex.css'
import './assets/css/iconfont.css'

import {
  Row,
  Col,
  Select,
  Option,
  Radio,
  Button,
  Input,
  Form,
  FormItem,
  Message,
  Tabs,
  TabPane,
  Dialog,
  Dropdown,
  DropdownMenu,
  DropdownItem,
  Pagination,
  Tag,
  Icon
} from 'element-ui';

Vue.use(Row);
Vue.use(Col);
Vue.use(Select);
Vue.use(Option);
Vue.use(Radio);

Vue.use(Button);
Vue.use(Input);
Vue.use(Form);
Vue.use(FormItem);
Vue.use(Tabs);
Vue.use(TabPane);
Vue.use(Dialog);
Vue.use(Dropdown);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Pagination);
Vue.use(Tag);
Vue.use(Icon);

Vue.prototype.$message = Message;

Vue.config.productionTip = false
Vue.use(mavonEditor)
Vue.use(VueLocalStorage);
Vue.use(VueLazyLoad, {
  error: '../static/chazuo.png',
  loading: '../static/chazuo.png'
})

router.beforeEach(async (to, from, next) => {
  // 获取用户信息
  let token = Vue.ls.get("CZXY_TOKEN");
  if (token) {
    const auth = {
      username: token
    }
    store.dispatch('user/auth', auth).then(() => {
      next()
    }).catch(ret => {
      setTimeout(() => {
        next()
      }, 500);
    })

  } else {
    // 判断是否需要登录
    if (!!to.meta.noAuth) {
      next()
    } else {
      // next('login')
    }
  }

});

router.afterEach(() => {

});

new Vue({
  el: '#app',
  router,
  store,
  components: {App},
  template: '<App/>'
})



