import teachers from '../../api/teachers'

const state = {
  // 讲师列表
  teacherList: [],
  // 讲师详情
  teacherDetails: {}
};

const mutations = {};

const actions = {
  // 获取讲师列表
  async getTeachersList({state, commit}, params) {
    let res = await teachers.list(params)
    state.teacherList = res.data
    return res
  },

  // 获取老师详细信息
  async getTeacherInfo({state, commit}, params) {
    let res = await teachers.getTeacherInfo(params)
    state.teacherDetails = res.data
    return res.data
  }
};

export default {
  namespaced: true,
  state,
  actions,
  mutations
}



