<template>
  <div id="app">
    <v-loading :text="loadingText" v-if="isLoading"/>
    <v-headers :userInfo="userInfo"/>
    <router-view class="router-views"/>
    <main-footer/>
  </div>
</template>

<script>
  import {mapState, mapGetters, mapActions} from 'vuex';
  import VLoading from './components/Loading'
  import VHeaders from './components/Headers'
  import MainFooter from './components/MainFooter'


  export default {
    components: {
      VLoading,
      VHeaders,
      MainFooter
    },
    data() {
      return {
        centerDialogVisible: true,
        showClose: true,
        userInfo: {},
      }
    },
    computed: {
      ...mapGetters('loading', [
        'isLoading',
        'loadingText'
      ])
    },
    mounted() {
      this._getUserInfo()
    },
    methods: {
      ...mapActions({
        userAuth: 'user/auth'
      }),
      _getUserInfo() {
        // 获取用户信息
        let token = this.$ls.get("CZXY_TOKEN");
        if (token) {
          const auth = {
            username: token
          }
          this.userAuth(auth).then((res) => {
            this.userInfo = res.data.data
          })
        }
      }
    }
  }
</script>

<style>

  html, body {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
  }

  body {
    font-family: "微软雅黑", "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", Arial, sans-serif;
    background: #f8f8f8;
  }

  #app {
  }

  section, article, header, nav, ul, li, h1, h2, h3 ,h4 ,h5 ,h6 ,p {
    margin: 0;
    padding: 0;
    font-weight: normal;
  }

  li {
    list-style: none;
  }

</style>



